<template>
	<div>
		<swiper :options="swiperOptions" class="swiper">
		   <swiper-slide v-for="(item,index) in swiperList" :key="index"><a :href="item.url" target="_blank"><img :src="item.src"/><!-- {{item.title}} --></a></swiper-slide>
		   <div class="swiper-pagination" slot="pagination"></div>
		 </swiper>
		 <br>
		 <Experiecnce/>
		 <!-- <div class="row skill">
			  <div class="col-md-6 col-xs-12">
				  <img src="../assets/About/主页服务.png"/>
				   <h2>努力精练技术 创新细致开发</h2>
			  </div>
		  </div> -->
	</div>
</template>

<script>
	import {Swiper,SwiperSlide,directive} from 'vue-awesome-swiper'
	import 'swiper/css/swiper.css'
	import Experiecnce from "@/components/Experience"
	export default {
		name: "Home",
		data() {
		  return {
			swiperOptions: {
			  autoplay:true,
			  pagination: {
				el: '.swiper-pagination',
				clickable:true,
			  }
			},
			swiperList:[]
		  }
		},
		components: {
			Swiper,
			SwiperSlide,
			Experiecnce
		},
		directives: {
			swiper: directive
		},
		mounted() {
		    const expJson = require('../../static/json/swiper.json');
		    this.swiperList=expJson.data;
		},
	}
</script>

<style scoped>
	.swiper img{
		width: 100%;
		min-height:12.5rem;
	}
	.skill{
		margin-top: 1.55rem;
	}
	h2{
		text-align: center;
		padding: 10px 0;
	}
	.skill img{
		height: auto;
	}
</style>



